import { Button, EditBox, EventTouch, Input, Label, Layout, Node, ScrollView, Sprite, SpriteFrame, Toggle, UIOpacity, instantiate } from "cc";
import { Second } from "../../utils/Utils";
import { ResMgr } from "../../manager/ResMgr";
import { CfgMgr } from "../../manager/CfgMgr";
import { Session } from "../../net/Session";
import { Panel } from "../../manager/GameRoot";
import { AutoScroller } from "../../component/AutoScroller";
import { PlayerData } from "../player/PlayerData";
import { Card } from "./Card";
import  base  from "../../net/proto/BaseUtils";
import { Protocol } from "../../net/Protocol";
import { MsgPanel } from "../common/MsgPanel";


export class CardsGroupChangePanel extends Panel {
    protected prefab: string = "prefabs/cards/CardsGroupChangePanel";
 
    protected tile: Label;
    protected scroller: ScrollView;
    private noneListCont:Node;
    private okBtn:Button;
    private clone_item: Node;
    protected limit = 0;
    protected datas: base.IBattleCard[];
    private selects = [];
    protected cardGroupScrollView: AutoScroller;
    private deckId

    protected onLoad() {
        this.CloseBy("closeBtn");
        this.noneListCont = this.find("noneListCont");
        this.scroller = this.find("ScrollView", ScrollView);
        this.clone_item = this.find("ScrollView/view/content/Cards");
        this.scroller.node.on('select', this.onSelect, this);
        this.find("okBtn", Button).node.on(Button.EventType.CLICK, this.onOk, this);
        this.find("deleteBtn", Button).node.on(Button.EventType.CLICK, this.onDelete, this);
        this.cardGroupScrollView = this.find("cardGroupScrollView", AutoScroller);
        this.cardGroupScrollView.SetHandle(this.updateCardGroupItem.bind(this));
        this.cardGroupScrollView.node.on('select', this.onSelectCardGroup, this);
    }

    protected onShow(): void {
        Session.Send(Protocol.getplayerdecks)
    }

    public flush(...args: any[]): void { 
        this.datas = [];
        for (const iterator of PlayerData.cards) {
            this.datas.push(iterator)
        }
        this.datas.sort((a:base.IBattleCard, b:base.IBattleCard)=>{
            let a_cost = CfgMgr.GetCard(a.cardId).Cost;
            let b_cost = CfgMgr.GetCard(b.cardId).Cost;
            return a_cost - b_cost;
        })
        this.scroller.content.removeAllChildren();
        for (const iterator of this.datas) {
            let card = instantiate(this.clone_item);
            card.on(Input.EventType.TOUCH_END, this.onSelect, this)
            this.updateCardItem(card, iterator);
            this.scroller.content.addChild(card);
        }
        const layout = this.scroller.content.getComponent(Layout);
        layout.enabled = false;
        layout.enabled = true;
        layout.updateLayout();
        this.scroller.scrollToTop(0.01)

        
      this.cardGroupScrollView.UpdateDatas(PlayerData.cardNameList);
    }
   


    protected updateCardItem(item: Node, data: base.IBattleCard) {
        let card = item.getComponent(Card);
        if (!card) card = item.addComponent(Card);
        card.SetData(data);
    }



    protected onOk() {
        let send_data = new Protocol.setdeckcards();
        send_data.cardIds = this.selects;
        send_data.deckId = this.deckId;
        Session.Send(send_data);
        this.selects = [];
        this.scroller.content.children.forEach(item =>{
            item.getComponent(Card).setSelect(false);
        })
    }

    protected onDelete() {
        let delete_data = new Protocol.deletedeck();
        delete_data.deckId = this.deckId;
        Session.Send(delete_data);
    }

    protected  onSelect(event:EventTouch) {
        let item = event.getCurrentTarget();
        let index = item.getSiblingIndex();
        let cards = item.getComponent(Card);
        let is_select = cards.getSelectState();
        cards.setSelect(!is_select)
        if(!is_select){
            this.selects.push(this.datas[index].id)
        }else{
           this.selects.splice(this.selects.indexOf(this.datas[index].id), 1)
        }
    }

    protected updateCardGroupItem(item: Node, data: base.IDeckAttrs) {
        let card_name = item.getChildByName("name").getComponent(Label);
        card_name.string = data.name;
        let card_id = item.getChildByName("id").getComponent(Label);
        card_id.string = data.id
    }

    protected  onSelectCardGroup(index: number, item: Node) {
        let card_name = item.getChildByName("name").getComponent(Label);
    
        let deckId = item.getChildByName("id").getComponent(Label);
        console.log(card_name.string , deckId.string)
        this.deckId = deckId.string;

        // let getdeckcards = new Protocol.getdeckcards();
        // getdeckcards.deckId = this.card_id;
        // Session.Send(getdeckcards);
    }


    protected onHide(...args: any[]): void {
    }

}
